<template>

  <div>
    <div v-touch="{
      left:swipeLeft
      }">
      <v-tabs fixed-tabs>
        <v-tab v-for="tab in tabs" :key="tab.id" >
          {{ tab }}
        </v-tab>
        <v-tabs-items :v-model="tabs">
          <v-tab-item
          v-for="tab in demandes" :key="tab.id" >
          {{tab}}
          <v-data-table
          :headers="headers"
          :items="tab"
          hide-actions
          class="elevation-1"
          >
          <template slot="items" slot-scope="props">
            <td >{{ props.item.distance }}</td>
            <td >{{ props.item.price }}</td>
            <td >{{ props.item.estimated_time }}</td>
          </template>
        </v-data-table>
      </v-tab-item>
    </v-tabs-items>

  </v-tabs>

</div>
<db-menu> </db-menu>
</div>

</template>


<script>


export default {


  data () {
    return {
      headers: [
          { text: 'Distance', value: 'distance' },
          { text: 'Prix', value: 'price' },
          { text: 'Temps estimé', value: 'estimated_time' }
        ],

      deviceid:'',
      tabs: [
        'En cours', 'Passées', 'Prévues'],
        demandes: [
          [],
          [],
          []
        ]
      }
    },


    mounted(){

      var self=this;
      $.ajax({
        url: 'http://dev-deliverbag.supconception.fr/mobile/deliveries/customers',
        type : 'POST',
        data : {"mobile_token" : localStorage.getItem('deviceId')},
        //localStorage.getItem('deviceId') pour avoir le vrai token de l'appareil
        success: function(data){
          console.log(data['Non payé']);
          for (let i=0;i<data['Non payé'].length;i++){
            self.demandes[0].push(data['Non payé'][i]);
          }
        },
        error:function(e){
          alert(e);
          console.log(e);
        }
      });

    },

    methods:{
      swipeLeft(){
        this.$router.replace({path: 'demand'});
      }
    }
  }


  </script>

  <style>
  </style>



